
      @use 'sass:math';
      @use 'sass:color';
      @import '/Users/nickmajor/projects/rw/care-ops-frontend/src/scss/provider-variables.scss';
    
.program-action--draft {
  color: $black-20;
  font-weight: bold;
}

.program-action--published,
.program-action--conditional {
  color: color(green);
  font-weight: bold;
}

.is-highlighted {
  .program-action--draft,
  .program-action--conditional,
  .program-action--published {
    color: $white;
  }
}


.is-disabled .program-action--published {
  color: $black-80;
}
